.hero-section {
    height: 300px;
    width: calc(100% - 20px);
    display: grid;
    /* grid-gap: 10px; */
    grid-template-columns: 70% 30%;
    background-color: #fff;
    box-sizing: content-box;
    padding: 10px;
    box-shadow: 2px 0px 20px rgba(255, 255, 255, 0.192);
}

.hero-section .right-section {
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.hero-section .right-section img {
    width: 100%;
    height: calc(100px - 7px);
}

.categories {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    color: #fff;
    gap: 10px;
}

.categories a {
    position: relative;
    display: flex !important;
    align-items: center !important;
    background-color: blueviolet;
    color: #ffffff;
    text-decoration: none;
    height: 100%;
    width: 100%;
    transform: skewY(-5deg);
    border-radius: 10px;
    cursor: pointer;
}


.categories a img {
    height: 65px;
    width: 65px;
    position: relative;
    display: block;
}

.categories a p {
    display: block;
    font-size: 1.2em;
    margin: 0em .7em 0em .7em;
}

.categories a svg {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.3em;
}

.categories a:hover {
    /* transform: scale(1.2); */
    background-color: rgb(110, 34, 182);

}

.categories a:hover img {
    transform: scale(1.2);
}

.categories a:hover svg {
    /* transform: scale(1.2); */
    color: #c2a7a7;
}

.flsh-sale .inner-product-collection {
    background-color: #0C1322;
    background-color: blueviolet;
    padding: 0 10px 20px 10px;
    border-radius: 10px;
}

 

.flsh-sale .inner-product-collection .count-section {
    display: flex;
    align-items: center;
    padding: 1.6em 1em;
    color: #fff;
}



.flsh-sale .inner-product-collection .count-section p {
    font-size: 1.2rem;
    font-weight: 900;
}

.flsh-sale .inner-product-collection .count-section .on-sale-now {
    color: yellow;
}

.flsh-sale .inner-product-collection .count-section .ending {
    margin: 0px 10px 0px 70px;
}

.flsh-sale .inner-product-collection .count-section .count-box {
    display: flex;
    align-items: center;
    gap: 10px;
}



.flsh-sale .inner-product-collection .count-section .count-box p {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    gap: 10px;
    background-color: #FF6801;
    font-weight: 800;
}


.service-colloction .inner-service-colloction, .product-collection .inner-product-collection {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    /* background-color: blueviolet; */
    background-color: #0C1322;
    padding: 20px 0px;
}

.product-section-title h3 {
    font-size: 1.7rem;
    font-weight: 800;
}

@media only screen and (min-width:200px) and (max-width: 1750px) {
    .categories {
        grid-template-columns: repeat(4, 1fr);
    }

    .categories a:nth-child(9) {
        display: none !important;
    }

    .categories a:nth-child(10) {
        display: none !important;
    }
}

@media only screen and (min-width:200px) and (max-width: 1250px) {
    .service-colloction .inner-service-colloction, .product-collection {
        padding: 0 18px;
    }

    .service-colloction .inner-service-colloction, .product-collection .inner-product-collection {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media only screen and (min-width:200px) and (max-width: 1000px) {
    .flsh-sale .inner-product-collection .count-section {
        padding: 1em .5em;
    }

    .categories a img {
        height: 45px;
        width: 45px;
        position: relative;
        display: block;
    }
}

@media only screen and (min-width:200px) and (max-width: 800px) {

    .hero-section {
        height: 410px;
        grid-template-columns: 100%;
    }

    .hero-section .right-section {
        padding: 0px;
        display: grid !important;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media only screen and (min-width:200px) and (max-width: 700px) {
    .hero-section {
        height: 310px;
    }

    .categories a {
        flex-direction: column;
        /* padding-bottom: 5px; */
    }

    .categories a svg {
        display: none;
    }

    .categories a p {
        text-align: center;
    }

    .service-colloction .inner-service-colloction, .product-collection .inner-product-collection {
        grid-template-columns: repeat(2, 1fr);
    }


}

@media only screen and (min-width:200px) and (max-width: 600px) {
    .hero-section {
        height: 280px;
        padding: 5px;

    }

    .flsh-sale .inner-product-collection .count-section p {
        font-size: 1.4rem;
    }

    .hero-section .right-section img {
        height: calc(100px - 30px);
    }
}


@media only screen and (min-width:200px) and (max-width:560px) {
    .hero-section {
        height: 220px;
        padding: 5px;

    }
}

@media only screen and (min-width:200px) and (max-width: 500px) {
    .categories {
        grid-template-columns: repeat(3, 1fr);
    }

    .categories a:nth-child(7) {
        display: none !important;
    }

    .categories a:nth-child(8) {
        display: none !important;
    }

    .flsh-sale .inner-product-collection .count-section .ending {
        margin-left: auto !important;
    }

    .flsh-sale .inner-product-collection .count-section .count-box {
        gap: 5px !important;
    }

    .flsh-sale .inner-product-collection .count-section .count-box p {
        height: 30px;
        width: 30px;
    }
}